import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { AmenitiesDataService } from './amenities-data.service';
import { AmenitiesGroupViewModel } from '../../../viewmodel/amenitiesviewmodel/amenitiesgroupviewmodel';
import { DateViewModel } from '../../../viewmodel/allocationsviewmodel/allocationdaterangeviewmodel';
import { AllocationsDataService } from './allocations-data.service';
import { ContractViewModel } from '../../../viewmodel/allocationsviewmodel/contractviewmodel';

@Injectable()
export class AllocationsResolverService implements Resolve<ContractViewModel> {

  constructor(private allocationsService: AllocationsDataService) {
  }

  date: DateViewModel = <DateViewModel>{};
  todaysDate = new Date();
  defaultMaxDate = new Date();

  resolve(route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<ContractViewModel> {
    const hotelId = route.parent.params['id'];

    this.defaultMaxDate.setDate(this.todaysDate.getDate() + 14);
    this.date.startDate = this.todaysDate.toISOString();
    this.date.endDate = this.defaultMaxDate.toISOString();
    this.date.hotelId = hotelId;
    return this.allocationsService.getStaticContractData(this.date);
  }

}



